import styled from "styled-components";

const Textarea = styled.div`
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 20px;
  border: none;
  color: white;
  background-color: #356c5d;

  &:focus {
    outline: none;
  }
`;

export default Textarea;



